.golive-wrapper{
    width: 100%;
    display: flex;
}
.golive-wrapper img{
    flex-basis: 45%;
    height: 30em;
    margin-top: 3em;
}
.golive-button{
    width:55%;
    height: 70px;
    margin: 10px 10px;
    border: none;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    font-size: 20px;
    border-radius: 10px;
}
.golive-button img{
    width: 30px;
    height: 30px;
    margin: 0;
}

.golive-button-wrapper{
    display: flex;
    flex-direction: column;
    flex-basis: 50%;
    align-items: center;
    margin-top: 1em;
    /* justify-content: center; */
}
.golive-schedulesession-modal{
    width: 50%;
    height: 80%;
    background-color: white;
    position: absolute;
    top: 50%;
    left: 50%;
    border-radius: 10px;
    overflow-y: scroll;
    transform: translate(-50%, -50%)    ;
}

.modal-content{
    padding: 20px;
    overflow-x: hidden;
}

.modal-close-row{
    /* padding: 10px; */
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.modal-close-row p{
    font-size: 25px;
    margin: 10px;
}

.modal-close-button{
    border: none;
    height: 50px;
    width: 100px;
    border-bottom-left-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 18px;
}
.modal-close-button img{
    margin:0 7px;
}

/* .modal-content{
    padding-bottom: 0 !important;
     overflow: scroll;
} */

.schedule-live-session-form input{
    display: block;
    /* padding: 10px  5px; */
    box-sizing: border-box;
    width: 100%;
    padding: 15px;
    margin:10px  0;
    background-color: #f5f5f5;
    border: none;
    border-radius: 5px;
    font-size: 18px;
}
.schedule-live-session-form textarea{
    margin: 0;
    display: block;
    /* padding: 10px  5px; */
    box-sizing: border-box;
    width: 100%;
    height: 100px;
    resize: vertical;
    /* max-height: 100px; */
    padding: 15px;
    background-color: #f5f5f5;
    border: none;
    border-radius: 5px;
    font-size: 18px;
}
.schedule-inline-wrapper{
    /* display: flex;
    justify-content: space-between; */
    background-color: white;
    border-radius: 10px;
}

.no-upcoming-session-wrapper{
    width: 80%;
    background-color: #e4e4e4;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    padding: 5%;
    margin: auto;
    border-radius: 15px;
}
.no-upcoming-session-wrapper p{
    text-align: center;
    font-size: 20px;
}
.no-upcoming-session-wrapper button{
    border: none;
    background-color: white;
    font-size: 20px;
    padding: 20px 20px;
    border-radius: 5px;
    margin: 20px 0;
}
.schedulebar-wrapper{
    width: 90%;
    margin: 10px auto;
    padding: 0 20px;
    box-shadow:0 0 10px 0 rgb(0 0 0 / 20%)
}
.schedulebar{
    display: flex;
    align-content: space-between;
    justify-content: space-between;
    padding: 10px 20px;

}
.schedulebar-wrapper label{
    position: relative;
    top:0%;
    left:-2%;
    border-top-left-radius : 4px;
    background-color: #ff9f1c;
    /* padding: 0 5px;
    margin-left:5px; */
}
.schedule-session-button{
    background-color: #ff9f1c;
    color: white;
    float: right;
}
.disabled{
    font-size:23px;
    color: red;
}