.header-wrapper{
    width: calc(100vw - 18%);;
    height: 60px;
    background-color: white;
    position:fixed;
}

.header-profile{
    height: inherit;
    float: right;
    display: flex;
    align-content: center;
    align-items: center;
    justify-items: center;
}

.header-profile img{
    width: 50px;
    margin: 0 10px;
    height: 50px;
    border-radius: 50px ;
}
.position{
    font-weight: 300;
}
