/* width */
::-webkit-scrollbar {
    width: 0px;
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: #f1f1f1;
  }
  
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #888;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555;
  }
  
.sidebar-wrapper{
    height: 100vh;
    width: 18%;
    overflow-y: scroll;
    background-color:#1462e1;
    /* position: fixed; */
}

.sidebar-wrapper ul{
    list-style-type: none;
    padding: 0;
    margin: 0;
    margin-top: 10px;
    /* display: grid; */
    align-items: center;
    justify-items: center;
}
.sidebar-wrapper ul li{
    display: flex;
    padding: 0 10px;
    margin: 0 10px;
    align-items: center;
    color: white;
}
.sidebar-wrapper ul li img{
    width: 30px;
    height: 25px;
    margin: 15px 15px;
}
.sidebar-wrapper ul li a{
    font-size: 1em;
    text-decoration: none;
    color: white;
}

.sidebar-link-active li{
  background-color: white;
  color: #1462e1 !important;
  /* border-top-left-radius: 50px;
  border-bottom-left-radius: 50px; */
  border-radius: 50px;
  box-shadow: 3px 3px 5px #888 inset,2px -3px 5px #888 inset;
  animation:  sidebar-animation 0.2s;
}


@keyframes sidebar-animation{
  0%{
    transform: translateY(-55px);
  }
  50%{
    transform: translateY(-25px);
  }
  100%{
    transform: translateY(0);
  }
}