.bookappointment-wrapper{
    width: 70%;
    display: flex;
    top: 50%;
    left: 50%;
    position: absolute;
    /* margin-right: -50%; */
    border-radius:20px;
    transform: translate(-50%, -50%);
    box-shadow: -4px -4px 6px 0 #b3b3b3, 4px 4px 6px 0 #b3b3b3;

}
.bookappointment-details{
    flex-basis: 50%;
}
.bookappointment-thumbnail{
    margin: 20px auto 0 auto;
    display: flex;
    border-radius: 5px;
    width: 350px;
    height: 250px;
}
.appointment-title{
    font-size: 25px;
}
.appointment-description{
    margin: 10px 0;
}

.appointmentDetails{
    display: flex;
    padding: 20px ;
    flex-basis: 70%;
    flex-direction: column;
    /* justify-items: flex-start; */
    align-items: flex-start;
    /* align-content: flex-start; */
    /* justify-content: start; */
    
}

.price-duration-wrapper{
    display: flex;
    width: 100%;
    align-content: center;
    align-items: center;
}

.appointment-price{
    display: flex;
    margin: 10px;
    align-items: center;
}
.appointment-price img{
  margin: 10px;
}
.appointment-duration{
    display: flex;
    align-items: center;
    /* margin: 10px; */
}
.appointment-duration img{
  margin: 10px;
}
.appointmentBooked-success{
  display: flex;
  height: 100%;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}
.appointmentBooked-success p{
  font-size: 20px;
  width: 70%;
  margin-top: 50px;
  text-align: center;font-weight: 500;
}
.appointmentBooked-success img{
  height: 200px;
}
.appointmentBooked-success button{
  font-size: 20px;
  width: 20%;
  height: 50px;
  border:none;
  background-color: #ff9f1c;
  border-radius: 10px;
  margin-top: 50px;
  text-align: center;
  font-weight: 500;
}

.time-wrapper{
  display: flex;
  height: 95%;
  justify-items: center;
  align-content: stretch;
  justify-items: center;
}
.time-box{
  max-width: 250px;
  border: 2px solid #757575;
  padding :10px;
  text-align: center;
  margin:10px auto;
}
.time-box-wrapper{
  flex-basis: 50%;
  overflow-y: scroll;

}
.time-image-wrapper{
  flex-basis: 50%;
  display: inline-flex;
  align-items: center;
  justify-items: center;
}
.time-image-wrapper img{
  max-width: 100%;
  margin: auto;
}
.time-box:hover{
  border: 2px solid #1087ff;
  color:#1087ff
}

.bookappointment-form{
  flex-basis: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;  
  margin-left: 10px;
  justify-content: center;
  align-content: center;
  justify-items: center;
}

.bookappointment-form input{
  width: 80%;
  margin: 5px 0px;
  padding:10px;
  margin-right: 10px;
  margin-bottom: 20px;
  border-radius: 50px;
  box-shadow: inset -6px -6px 9px 0 #fff, inset 4px 4px 10px 0 #b3b3b3;
  font-family: Gilroy, sans-serif;
  font-weight: 500; 
}
.bookappointment-form p{
  text-align: center;
  margin: 10px 0;
}


.confirm{
  margin-top: 10px;
  padding:10px 15px;
  padding-right: 18px;
  padding-left: 18px;
  border-style: none;
  border-width: 0px;
  border-color: #b3b3b3;
  border-radius: 20px;
  background-color: #e0e5ec;
  box-shadow: -3px -3px 8px 2px #f5f5f5, 3px 3px 8px 2px #a3b1c6;
  font-family: Gilroy, sans-serif;
  max-width: 150px;
  text-align: center;
}

.modal-close{
  height: 5%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.modal-close div img{
  margin: auto;
  margin-right: 10px;
}
.modal-close img{
  margin: 10px;
}

.bookappointment-modal-overlay{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgb(0,0,0, 0.75);
}

.bookappointment-modal{
    width:50%;  
    height:60%;
    position: relative;
    top: 50%;
    left: 50%;
    background-color: white;
    padding: 20px;
    border-radius: 20px;
    transform: translate(-50%, -50%);
}

.react-calendar {
    flex-basis: 50%;
    /* background-color: white; */
    border: 0px solid #a0a096;
    /* border-top-right-radius : 50px;
    border-bottom-right-radius : 50px; */
    box-sizing: border-box;
    font-family: Arial, Helvetica, sans-serif;
    /* line-height: 1.125em; */
  }

  .react-calendar--doubleView {
    width: 700px;
  }
  .react-calendar--doubleView .react-calendar__viewContainer {
    display: flex;
    margin: -0.5em;
  }
  .react-calendar--doubleView .react-calendar__viewContainer > * {
    width: 50%;
    margin: 0.5em;
  }
  
   .react-calendar,
  .react-calendar *,
  .react-calendar *:before,
  .react-calendar *:after {
    -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
  }
  .react-calendar button {
    margin: 0;
    border: 0;
    outline: none;
  }
  .react-calendar button:enabled:hover {
    cursor: pointer;
  }
  .react-calendar__navigation {
    height: 60px;
    /* margin-bottom: 1em; */
  }
  .react-calendar__navigation button {
    min-width: 44px;
    background: none;
  }
  .react-calendar__navigation button:enabled:hover,
  .react-calendar__navigation button:enabled:focus {
    background-color: #e6e6e6;
  }
  .react-calendar__navigation button[disabled] {
    background-color: #f0f0f0;
  }
  .react-calendar__month-view__weekdays {
    text-align: center;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 1em;
    color: #1462E1;
    text-decoration: none;
  }
  .react-calendar__month-view__weekdays__weekday {
    padding: 0.5em;
  }
  .react-calendar__month-view__weekNumbers {
    font-weight: bold;
  }
  .react-calendar__month-view__weekNumbers .react-calendar__tile {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0.75em;
    padding: calc(0.75em / 0.75) calc(0.5em / 0.75);
  }
  .react-calendar__month-view__days__day--weekend {
    color: #d10000;
  }
  .react-calendar__month-view__days__day--neighboringMonth {
    color: #757575;
  }
  .react-calendar__year-view .react-calendar__tile,
  .react-calendar__decade-view .react-calendar__tile,
  .react-calendar__century-view .react-calendar__tile {
    padding: 2em 0.5em;
  }
  .react-calendar__tile {
    /* max-width: 100px; */
    /* width: 100px; */
    height:70px;
    text-align: center;
    padding: 0.75em 0.5em;
    background: none;
    font-size: 20px;
  }
  .react-calendar__tile:disabled {
    background-color: #f0f0f0;
  }
  .react-calendar__tile:enabled:hover,
  .react-calendar__tile:enabled:focus {
    background-color: #ff9f1c;
    /* color: #ff9f1c; */

    /* border-radius: 15px; */
  }
  .react-calendar__tile--now {
    background: #ffff76;
  }
  .react-calendar__tile--now:enabled:hover,
  .react-calendar__tile--now:enabled:focus {
    background: #ffffa9;
  }
  .react-calendar__tile--hasActive {
    background: #76baff;
  }
  .react-calendar__tile--hasActive:enabled:hover,
  .react-calendar__tile--hasActive:enabled:focus {
    background: #a9d4ff;
  }
  .react-calendar__tile--active {
    background: #006edc;
    /* color: white; */
  }
  .react-calendar__tile--active:enabled:hover,
  .react-calendar__tile--active:enabled:focus {
    background: #1087ff;
  }
  .react-calendar--selectRange .react-calendar__tile--hover {
    background-color: #ff5252;
  }
  
  .rc_body-days-of-month .rc_body-cell .rc_body-cell_value {
    /* border-radius: 50%; */
  }
  .react-calendar, .react-calendar *, .react-calendar *:before, .react-calendar *:after{
      background-color: none !important;
  }