.manage-member-wrapper{
    overflow-x: hidden;

}
.manage-member-row{
    display: flex;
    justify-content: space-between;
}
.manage-member-icon{
    width: 30%;
    margin-right: 30px;
}

.manage-member-row div{
    flex-basis: 50%;
    margin-right: 20px;
}

.manage-member-row div h1{
    margin: 20px;        
}

.managemember-button{
    max-width: 180px;
    display: flex;
    background-color: #f5f5f5;
    padding: 20px;
    margin-right: 10px;
    justify-content: space-evenly;
    font-size: 20px;
    border-radius: 10px;
    box-shadow: -4px -4px 6px 0 #fff, 4px 4px 6px 0 #b3b3b3;
}

.managemember-button img{ 
    width: 25px;
}

.createmember-modal{
    width: 25%;
    height: 30%;
    background-color: white;
    position: absolute;
    top: 50%;
    left: 50%;
    border-radius: 10px;
    overflow-y: scroll;
    padding: 15px;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    align-content: center;
    align-items: center;
}

.createmember-modal p{
    font-size: 25px;
    font-weight: 500;
}

.creategroup-button{
    background-color: #ff9f1c;
}

.addmember-modal{
    width: 35%;
    height: 50%;
    background-color: white;
    position: absolute;
    top: 50%;
    left: 50%;
    border-radius: 10px;
    overflow-y: scroll;
    padding: 15px;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.addmember-modal p{
    font-size: 25px;
}

.filter-container{
    display: flex;
    font-size: 20px;
}
.filter-container img{
    width: 20px;
    margin: 10px;
}

.search-filter-container{
    display: flex;
    margin: 10px;
    align-items: center;
}
.searchresult-wrapper{
    margin:20px;
    overflow-x: auto;
}

.searchresult-wrapper table{
    width: 100%;
}

/* .searchresult-wrapper table thead{
    display: table;
    width: 100%;
}
.searchresult-wrapper table tbody{
    display: table;
    width: 100%;
} */

.searchresult-wrapper table tr:nth-child(even){
    background-color: #f2f2f2;
}
.searchresult-wrapper table tr:hover{
    background-color: #ddd;
}
.searchresult-wrapper th{
        padding-top: 5px;
        padding-bottom: 5px;
        text-align: center;
        background-color: #ff9f1c;
        color: white;
}

.searchresult-wrapper table td{
    padding: 10px 0px;
    /* width: min-content; */
    /* min-width: 150px; */
    text-align: center;
}

.searchresult-wrapper table tr:first{
    max-width: 50px;
    background-color: bisque;
}
.editButton{
    border: none;
    background-color: greenyellow;
    padding: 5px 10px;
    margin: 0 10px;

}
.removeButton{
    border: none;
    background-color: red;
    padding: 5px 10px;
    margin: 0 10px;
    color: white;
}