.setting-wrapper{
    padding: 0 50px;
}

.account-setting-form{
    margin: 100px;
    display: flex;
    flex-direction: column;
}

.inline{
    display: flex;
    align-items: center;
}

.account-setting-form input{
    margin:10px 15px;
    padding: 15px;
    max-width: 50%;
    background-color: #f5f5f5;
    border: none;
    border-radius: 5px;
    font-size: 18px;

}

.save-changes-button{
    height: 50px;
    width: 20%;
    padding: 10px 30px;
    margin:10px 15px;
    background-color: #00da00;
    border:none;
    border-radius: 50px;
    font-size: 18px;
}

.first-last-name-wrapper{
    display: flex;
}

.first-last-name-wrapper input{
    width: 50%;
    
}
.subdomain-wrapper{
    display: flex;
    align-items: center;

}
.subdomain-wrapper input{
    width: 30%;
}
.subdomain-wrapper h1{
    font-weight: 500;
    font-size: 20px;
}
.subdomain-wrapper p{
    margin: 0 20px;
    color: #888;
}

.website-settings{
    padding: 20px 100px;
    margin-bottom: 50px;
    background-color: #e2e2e2;
    border-radius: 10px;
}

.website-settings-form input{
    margin:10px 0px;
    padding: 15px;
    width: 40%;
    background-color: #f5f5f5;
    border: none;
    border-radius: 5px;
    font-size: 18px;

    /* max-width: 50%; */
}
.website-settings-form p{
    font-size: 20px;
}
.send-request-button{
    height: 50px;
    width: 20%;
    padding: 10px 30px;
    margin:10px 15px;
    /* background-color: #00da00; */
    background-color: white;
    border:none;
    border-radius: 10px;
    font-size: 18px;
}