.support-button-flex{
    display: flex;
    width: 100%;
}

.support-button-flex a{
    display: flex;
    margin: 0 20px;
    width: 300px;
    height:50px;
    border:none;
    align-items: center;
    background-color: #f5f5f5;
    color: #1462e1;
    justify-content: space-around;
    border-radius: 10px;
    font-size: 18px;
} 

.support-button-flex a img{
    width: 40px;
    height: 30px;
}

.help-support-content{
    margin:0 50px;
    display: flex;
}
.help-support-content img{
    width: 50%;
    height: 400px;
}

.help-and-support-form-wrapper{
    padding:10px;
    display: flex;
    width: 50%;
    flex-direction: column;
    justify-content: center;
}
.help-and-support-form-wrapper input,textarea{
    padding:10px;
    width: 80%;
    background-color: #f5f5f5;
    border: none;
    border-radius: 5px;
    font-size: 18px;
    height: 30px;
    margin: 5px 10px;
}
.get-in-touch{
    width: 100px;
    border: none;
    height: 40px;
    margin:10px;
    background-color: #1462e1;
    color: white;
    border-radius: 5px ;
}
.inline{
    display: flex;
}