.gotopage-wrapper{
    display: flex;
}
.gotopage-details{
    flex-basis: 50%;
}
.detail-block{
    background-color: #f5f5f5;
    padding: 10px;
    border-radius:5px;
    margin:10px;
    width: 40%;
}
.detail-block > .title{ 
    font-weight: 500;
    
}

.gotopage-iframe{
    /* width: 45%; */
    flex-basis: 45%;
    margin: 25px;
    border-radius: 10px;
    height: 22em;
}

.round-button{
    border-radius:50px;
    border:none;
    padding: 10px 15px;

}