.searchBar{
    width: 100%;
    margin: 10px 40px;
    display: flex;

}

.resource-wrapper{
    overflow-x: hidden;
}

.searchBar input{
    width: 50%;
    height: 40px;
    padding: 0 10px;
    border-top-left-radius: 50px;
    border-bottom-left-radius: 50px;
}

.searchBar button{
    border: none;
    height: 43px;
    width: 150px;
    color: white;
    border-top-right-radius: 50px;
    border-bottom-right-radius: 50px;
    background-color: #ff9f1c;
}

.upload-button{
    font-size: 20px;
}

.upload-button img{
    transform: rotate(180deg);
    width: 30px;
    margin: 0 10px;
}

.file-Wrapper{
    max-width: 22%;
    margin: 10px;
}

.file-Wrapper img{
    width: 100%;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
}
.file-Wrapper p{
    font-weight: 500;
}
.recent-file-row{
    display: flex;
    width: 100%;
    margin-left: 20px;
    justify-content: flex-start;
    align-items: flex-start;
    /* overflow-x: scroll; */
}

.inline-file-wrapper{
    margin-left: 15px;
    font-size: 18px;
    display: flex;
    margin-top:15px;
    padding: 5px 0;
}

.inline-file-wrapper p{
    flex-basis: 60%;
}

.inline-file-wrapper .options{
    flex-basis: 30%;
}
.inline-file-wrapper img{
    margin-right: 10px;
}

::-webkit-scrollbar {
    width: 2px;
    height: 4px;
    /* display: none; */
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    background: #f1f1f1;
  }
  
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #888;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555;
  }
  .folder-wrapper{
      display: grid;
      grid-template-columns: auto auto auto auto;
      grid-column-start: 1;
      grid-column-end: 3;
      grid-row-start: 1;
      grid-row-end: 3;
      margin: 20px;
  }
  .folder{
    width: 250px;
    height: 200px;
    border-radius: 10px;
    margin: 10px 0;
    background-color: #f1f1f1;    
  }

  .folder img{
      width: 100px;
      position: relative;
      top:50%;
      right: -50%;
      transform: translate(-50px,-50px);
  }
  .folder p{
      position: relative;
      bottom: -45%;
      left: 4%;
      font-size: 18px;
  }
  .create-folder-modal{
    width: 30%;
    height: 30%;
    background-color: white;
    position: absolute;
    top: 50%;
    left: 50%;
    border-radius: 10px;
    overflow-y: scroll;
    padding: 20px;
    transform: translate(-50%, -50%)    ;
  }

  .create-folder-modal p{
      font-size: 25px;
      text-align: center;
  }
  .create-folder-modal form{
      display: flex;
      flex-direction: column;
      /* align-items: center; */
      justify-content: space-evenly;
  }

  .create-folder{

      background-color: #ff9f1c;
  }
